*,body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

body {
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    line-height: 24px;
    background-color: #151427;
    color: $color-white;
}

h1,h2,h3,h4,h5,p {
    margin: 0;
    padding: 0;
}

h1 {
    font-family: 'Orbitron', sans-serif;
    font-size: 48px;
    line-height: 60px;
    font-weight: 800;
    text-transform: uppercase;
    @include sm-block {
        font-size: 32px;
        line-height: 40px;
    }
}

h2 {
    font-family: 'Orbitron', sans-serif;
    font-size: 36px;
    line-height: 45px;
    font-weight: 800;
    text-align: center;
    text-transform: uppercase;
    @include sm-block {
        font-size: 28px;
        line-height: 35px;
    }
}

.container {
    max-width: 1202px;
    // 1121
    padding: 0 40px;
    margin: 0 auto;
    @include md-block {
        padding: 0 20px;
    }
    @include sm-block {
        padding: 0 15px;
    }
}

ul {
    padding-left: 0;
}

li {
    padding-left: 0;
    list-style: none;
}

img {
    max-width: 100%;
}

a {
    color: $color-text;
    text-decoration: none; 
    &:hover {
        color: $color-primary;
    }
}