.about {
    padding-bottom: 80px;
    .page-head {
        background: url('../images/about-bg.png') center center / cover no-repeat;
    }
}

.about__cards  {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include md-block {
        grid-template-columns: 1fr;
        gap: 64px;
        max-width: 472px;
        margin: 0 auto;
    }
    @include sm-block {
        gap: 40px;
    }
}

.advantages__title {
    margin-bottom: 64px;
}
.advantages__list  {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 85px;
    @include md-block {
        grid-template-columns: repeat(2, 1fr);
        gap: 96px 24px;
    }
    @include sm-block {
        grid-template-columns: 1fr;
        gap: 80px;
    }
}

.advantages__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    @include md-block {
        &:last-of-type {
            grid-column: span 2;
        }
    }
    @include sm-block {
        &:last-of-type {
            grid-column: span 1;
        }
    }
}

.advantages__item-img {
    margin-bottom: 31px;
}

.advantages__btn {
    max-width: 152px;
    display: block;
    margin: 48px auto 0;
    text-transform: uppercase;
    white-space: nowrap;
    padding: 0 32px;
}