.events {
    padding-top: 96px;
    .container {
        max-width: 1290px;
    }
    @include sm-block {
        padding: 80px 15px 0;
    }
}

.events__title {
    max-width: 664px;
    margin: 0 auto 64px;
    @include sm-block {
        margin-bottom: 48px;
    }
}

.events__slider {
    padding: 0 84px;
    position: relative;
}


.events__slider-wrapper {
    overflow: hidden;
    position: relative;
    padding-bottom: 36px;
}

.event__slider-img {
    margin-bottom: 16px;
    border-radius: 12px;
    overflow: hidden;
    img {
        width: 100%;
        display: block;
    }
    @media(max-width: 960px) {
        img {
            height: 258px;
            object-fit: cover;
        }
    }
}

.event__slider-title {
    margin-bottom: 16px;
    font-size: 20px;
    line-height: 1.3;
    letter-spacing: .3px;
}

.event__slider-date {
    font-size: 14px;
    line-height: 1.3;
    color: $color-text;
}

.events__cards {
    max-width: 990px;
    padding: 96px 0 64px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 90px;
    @include md-block {
        max-width: 472px;
        grid-template-columns: 1fr;
        gap: 64px;
    }
    @include sm-block  {
        gap: 40px;
        padding: 80px 0 40px;
    }
}