.blog {
    padding: 48px 0 64px;
    @include md-block {
        padding: 48px 0 80px;
    }
    @include sm-block {
        padding: 60px 0 80px;
    }
}

.blog__title {
    margin-bottom: 48px;
    @include md-block {
        margin-bottom: 64px;
        text-align: center;
    }
    @include sm-block {
        margin-bottom: 40px;
    }
}

.blog__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 58px;
    gap: 30px;
    &:last-of-type {
        margin-bottom: 0;
    }
    &--xl {
        display: grid;
        grid-template-columns: 1fr 1fr;
        .blog__item-img {
            max-width: 100%;
            background: linear-gradient(rgba(156, 255, 160, 1) ,rgba(177, 162, 247, 1) , rgba(155, 64, 247, 1));
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 4px;
            img {
                width: calc(100% - 4px);
                height: calc(100% - 4px);
                margin: 2px;
                display: block;
            }
        }
    }
    @include md-block {
        &--xl {
            grid-template-columns: 1fr;
        }
        gap: 24px;
        margin-bottom: 48px;
    }
    @include sm-block {
        display: grid;
        grid-template-columns: 1fr;
        gap: 24px;
        margin-bottom: 40px;
    }
}

.blog__item-img {
    max-width: 354px;
    width: 100%;
    img {
        width: 100%;
        display: block;
    }
    @include sm-block {
        max-width: 100%;
    }
}

.blog__item-date {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 16px;
    color: $color-text;
}

.blog__item-desc {
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 16px;
    @include sm-block {
        font-size: 16px;
        line-height: 24px;
    }
}

.blog-pagination {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-top: 64px;
}

.pagination__item {
    width: 32px;
    height: 32px;
    border: 1px solid #616161;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    &-num {
        border-color: $color-secondary;
        background-color: $color-secondary;
        color: $color-white;
    }
}

.pagination__text {
    display: flex;
    align-items: center;
    color: $color-text;
    font-size: 16px;
    line-height: 22px;
    span {
        display: block;
        margin-left: 5px;
    }
}