.advertisers {
    .page-head {
        background: url('../images/advertisers-bg.png') center center / cover no-repeat;
    }
}

.advertisers__cards {
    padding-top: 64px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 90px;
    max-width: 990px;
    margin: 0 auto;
    @include md-block {
        grid-template-columns: 1fr;
        max-width: 472px;
        margin: 0 auto;
        gap: 64px;
    }
    @include sm-block {
        gap: 40px;
    }
}