.partners {
    padding-bottom: 64px;
    .container {
        max-width: 1270px;
        padding: 0;
        position: relative;
        @include md-block {
            padding: 0 20px;
        }
    }
}

.partners__title {
    margin-bottom: 64px;
}

.partners__list {
    position: relative;
    padding: 0 76px;
    @include md-block {
        padding: 0 99px;
    }
    @include sm-block {
        padding: 0;
    }
}

.partners__slider {
    overflow: hidden;
    position: relative;
    padding-bottom: 36px;
}

.partners__item {
    padding: 24px 24px 46px;
    background: linear-gradient(137.71deg, rgba(77, 43, 146, 0.2) -7.29%, rgba(146, 98, 241, 0.05) 109.93%);
    border-radius: 4px;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    z-index: 1;
    // &::before {
    //     position: absolute;
    //     content: '';
    //     width: calc(100% + 2px);
    //     height: calc(100% + 2px);
