    //     right: 1px;
    //     left: 1px;
    //     top: 1px;
    //     bottom: 1px;
    //     z-index: -1;
    // }
}


.partners__item-img {
    margin-bottom: 24px;
}

.partners__item-desc {
    text-align: center;
    font-size: 16px;
    line-height: 1.5;
}