.contacts {
    .page-head {
        background: url('../images/contacts-bg.png') center center / cover no-repeat;
    }

    .page-head__title {
        margin-bottom: 24px;
    }
}

.contacts-feed {
    padding-top: 64px;
    padding-bottom: 80px;
    @include sm-block {
        padding: 40px 0 80px;
    }
}


.contacts-feed__title {
    margin-bottom: 48px;
}

.contacts__form {
    max-width: 630px;
    width: 100%;
    margin: 0 auto;
}