.page-head {
    padding: 192px 0 0 0;
    min-height: 480px;
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    @include sm-block {
        padding: 158px 0 0 0;
        height: 356px;
        min-height: initial;
    }
}

.page-head__title {
    margin-bottom: 40px;
    @include sm-block {
        margin-bottom: 24px;
    }
}

.page-section + .page-section {
    margin-top: 128px;
    @include sm-block {
        margin-top: 80px;
    }
}

.page-section__title {
    margin-bottom: 64px;
}

.page-head__desc {
    max-width: 438px;
    font-size: 20px;
    line-height: 1.4;
    letter-spacing: .3px;
    color: $color-text;
    margin-bottom: 57px;
}

.page-head__scroll {
    display: flex;
    align-items: center;
    flex-direction: column;
    span {
        text-transform: uppercase;
        font-family: 'Orbitron';
        font-size: 12px;
        line-height: 1.2;
        color: $color-text;
        margin-bottom: 4.5px;
    }
    @include sm-block {
        display: none;
    }
}