.hero {
    background: url('../images/hero-bg.png') center calc(100% - 30px) / cover no-repeat;
}

.hero-content {
    display: flex;
    align-items: center;
    padding-bottom: 111px;
    @include section-divider();
    @include md-block {
        flex-direction: column;
        text-align: center;
        padding-bottom: 195px;
    }
}

.hero__title {
    font-family: 'Orbitron', sans-serif;
    font-size: 48px;
    line-height: 60px;
    text-transform: uppercase;
    margin-bottom: 16px;
    @include sm-block {
        font-size: 24px;
        line-height: 30px;
    }
}

.hero__info {
    max-width: 479px;
}

.hero__desc {
    font-size: 20px;
    line-height: 28px;
    color: $color-text;
    @include sm-block {
        font-size: 16px;
        line-height: 24px;
    }
}

.hero__scroll {
    position: absolute;
    width: auto;
    display: block;
    left: 50%;
    bottom: 80px;
    transform: translateX(-50%);
    cursor: pointer;

    span {
        display: block;
        font-family: 'Orbitron', sans-serif;
        text-align: center;
        text-transform: uppercase;
        font-size: 12px;
        line-height: 15px;
        margin-bottom: 8px;
        color: $color-text;
    }
    svg {
        display: block;
        margin: 0 auto;
        transform: translateY(0);
        transition: .2s;
    }
    &:hover {
        opacity: .7;
        svg {
            transform: translateY(5px);
            transition: .5s;
        }
    }
}

.company {
    
}

.company-content {
    display: flex;
    align-items: center;
    padding: 84px 0 119px;
    @include section-divider();
    @include md-block {
        padding: 64px 0;
        flex-direction: column;
    }
}

.company__info {
    max-width: 520px;
    width: 100%;
    margin-right: 15px;
    @include md-block {
        max-width: 342px;
        margin: 0 auto;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.company__desc {
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 40px;
    color: $color-text;
    p {
        + p {
            margin-top: 24px;
        }
    }
    @include sm-block {
        font-size: 16px;
        line-height: 24px;
    }
}

.company__img {
    @include sm-block {
        display: none;
    }
}

.benefits {
    padding: 100px 0 142px;
    @include md-block {
        padding: 96px 0 128px;
    }
    @include sm-block {
        padding: 80px 0;
    }
}

.benefits__title {
    margin-bottom: 64px;
    @include sm-block {
        margin-bottom: 54px;
    }
}

.benefits-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 75px 85px;
    @include md-block {
        gap: 96px 23px;
        grid-template-columns: 1fr 1fr;
    }
    @include sm-block {
        grid-template-columns: 1fr;
        gap: 80px;
    }
}

.benefits__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.benefits__item-desc {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: .3px;
}

.benefits__item-title {
    font-size: 18px;
    line-height: 32px;
    font-weight: 500;
}

.main-bottom {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    
    @include md-block {
        grid-template-columns: 1fr;
        overflow-x: hidden;
        
    }
}

.main-bottom__item {
    padding: 64px 66px;
    position: relative;
    &--left {
        background: url('../images/affiliates-left.png') center center / cover no-repeat;
        .main-bottom__info {
            max-width: 410px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 0 90px 0 auto;
        }
        .main-bottom__img {
            top: 22px;
            left: 14%;
        }
    }
    &--right {
        background: url('../images/affiliates-right.png') center center / cover no-repeat;
        .main-bottom__info {
            max-width: 520px;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 0 auto 0 35px;
        }
        .main-bottom__img {
            right: 0;
            bottom: -40%;
        }
    }
    
    @media(max-width: 1919px) {
        &--left {
            .main-bottom__info {
                margin: 0 auto;
            }
            .main-bottom__img {
                max-width: 189px;
                left: -100px;
                top: 22px;
                img {
                    width: 100%;
                    display: block;
                }
            }
        }
        &--right {
            .main-bottom__info {
                margin: 0 auto;
            }
            .main-bottom__img {
                max-width: 262px;
                right: 0px;
                bottom: -120px;
                img {
                    width: 100%;
                    display: block;
                }
            }
        }
    }
    @include md-block {
        &--left {
            .main-bottom__info {
                margin: 0 auto;
            }
            .main-bottom__img {
                max-width: 134px;
                left: -50px;
                top: 58px;
                img {
                    width: 100%;
                    display: block;
                }
            }
        }
        &--right {
            .main-bottom__info {
                margin: 0 auto;
            }
            .main-bottom__img {
                max-width: 232px;
                right: -80px;
                bottom: 0px;
                img {
                    width: 100%;
                    display: block;
                }
            }
        }
    }
    @include sm-block {
        &--left {
            padding: 84px 15px;

            .main-bottom__img {
                display: none;
            }
        }
        &--right {
            padding: 74px 15px;

            .main-bottom__img {
                display: none;
            }
        }
    }
}

.main-bottom__title {
    margin-bottom: 24px;
}

.main-bottom__desc {
    margin-bottom: 40px;
    letter-spacing: .3px;
    font-size: 20px;
    line-height: 32px;
    color: $color-text;
    @include sm-block {
        font-size: 16px;
        line-height: 24px;
    }
}

.main-bottom__img {
    position: absolute;
}

.main-bottom__btn {
    height: 48px;
    padding: 8px 24px;
    display: flex;
    align-items: center;
    font-family: 'Raleway', sans-serif;
    font-size: 18px;
    line-height: 32px;
    font-weight: 600;
    svg {
        display: block;
        margin-left: 8px;
    }
}