.card {
    padding: 48px 32px;
    background: linear-gradient(137.71deg, rgba(36, 18, 73, 0.2) -7.29%, rgba(146, 98, 241, 0.05) 109.93%);
    backdrop-filter: blur(12px);
    border-radius: 12px;
    position: relative;
    &::before {
        position: absolute;
        content: '';
        top: -2px;
        left: -2px;
        right: -2px;
        bottom: -2px;
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        background: linear-gradient(rgba(104, 58, 197, 0.068)
        rgba(202, 177, 255, 0.05)
        rgba(54, 40, 83, 0.066));
        border-radius: 12px;
    }
}

.card__line {
    margin-bottom: 16px;
    width: 100%;
    height: 20px;
    position: relative;
    &::before {
        position: absolute;
        width: 100%;
        height: 100%;
        content: '';
        background: linear-gradient(90deg, rgba(186, 18, 245, 0.6) 0%, rgba(217, 217, 217, 0) 100%, rgba(186, 18, 245, 0) 100%);
        opacity: 0.2;
        left: 12px;
    }
    &--purple {
        &::before {
            background: linear-gradient(90deg, rgba(186, 18, 245, 0.6) 0%, rgba(217, 217, 217, 0) 100%, rgba(186, 18, 245, 0) 100%);
        }
        .card__line-dot {
            background: #BA12F5;
            box-shadow: 0px 0px 4px rgba(186, 18, 245, 0.5);
        }
    }
    &--blue {
        &::before {
            background: linear-gradient(90deg, rgba(17, 245, 204, 0.6) 0%, rgba(217, 217, 217, 0) 100%, rgba(17, 245, 204, 0) 100%);
        }
        .card__line-dot {
            background: #12F5CC;
            box-shadow: 0px 0px 4px rgba(18, 245, 204, 0.5);
        }
    }
    &--green {
        &::before {
            background: linear-gradient(90deg, rgba(18, 245, 122, 0.6) 0%, rgba(217, 217, 217, 0) 100%, rgba(18, 245, 122, 0) 100%);
            opacity: 0.2;
        }
        .card__line-dot {
            background: #12F57A;
            box-shadow: 0px 0px 4px rgba(18, 245, 122, 0.5);
        }
    }
}
.card__line-dot {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    background: #BA12F5;
    opacity: 1;
    box-shadow: 0px 0px 4px rgba(186, 18, 245, 0.5);
    border-radius: 50%;
    z-index: 1;
}

.card__title {
    margin-bottom: 16px;
    font-family: 'Orbitron', sans-serif;
    font-size: 22px;
    line-height: 32px;
}

.card__desc {
    font-size: 20px;
    line-height: 28px;
    color: $color-text;
    @include sm-block {
        font-size: 16px;
        line-height: 24px;
    }
}