.form__input {
    width: 100%;
    padding: 12px 16px;
    height: 48px;
    font-family: 'Raleway';
    font-size: 18px;
    line-height: 24px;
    color: $color-white;
    background: #0D1318;
    border: 1px solid $color-gray;
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.25);
    outline: none;
    border: 1px solid transparent;
    border-radius: 2px;
    &::placeholder {
        font-size: 18px;
        ;line-height: 24px;
        color: $color-gray;
    }
    &:focus {
        border: 1px solid $color-gray;
        &::placeholder {
            display: none;
            opacity: 0;
        }
    }
    &._fill {
        border: 1px solid $color-gray;
    }
}

.form__textarea {
    box-sizing: border-box;
    max-width: 630px;
    width: 100%;
    padding: 12px 16px;
    height: 144px;
    font-family: 'Raleway';
    font-size: 18px;
    line-height: 24px;
    color: $color-white;
    background: #0D1318;
    border: 1px solid $color-gray;
    box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.25);
    border: 1px solid transparent;
    outline: none;
    border-radius: 2px;
    &::placeholder {
        font-size: 18px;
        line-height: 24px;
        color: $color-gray;
    }
    &:focus {
        border: 1px solid $color-gray;
        &::placeholder {
            display: none;
            opacity: 0;
        }
    }
    &._fill {
        border: 1px solid $color-gray;
    }
}

.form__item {
    margin-bottom: 19px;
}

.form__btn {
    margin-top: 24px;
    .btn {
        max-width: 100%;
        width: 100%;
    }
}