.header {
    width: 100%;
    padding: 24px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &--secondary {
        position: absolute;
        border-bottom: none;
        top: 0;
        left: 0;
        right: 0;
    }
    .container {
        max-width: 1370px;
        width: 100%;
        padding: 0 40px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    @include md-block {
        .container {
            padding: 0 20px;
        }
    }
    @include sm-block {
        .container {
            padding: 0 15px;
        }
    }
} 
.header-nav {
    max-width: 703px;
    width: 100%;
    display: flex;
    align-items: center; 
    @include lg-block {
        display: none;
    }
}
.header-nav__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 600px;
    width: 100%;
}

.header-nav__item {
    font-size: 16px;
    line-height: 24px;
    a {
        color: $color-text;
        &:hover {
            color: $color-primary;
        }
    }
}

.header__lang {
    margin-left: 60px;
}

.header__btn {
    @include lg-block {
        margin: 0 76px 0 auto;
    }
    @include sm-block {
        display: none;
    }
}

.header__logo {
    @include sm-block {
        height: 48px;
        img {
            width: auto;
            height: 100%;
        }
    }
}


.header__burger  {
    display: none;
    @include lg-block {
        display: block;
        width:  30px;
        height: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: $color-white;
        }
    }
}