.blog-head {
    max-width: 1122px;
    margin: 0 auto;
    width: 100%;
    position: relative;
    min-height: 384px;
    .container {
        max-width: 1010px;
    }
    @include sm-block {
        min-height: 320px;
    }
}

.blog-head__img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    img {
        display: block;
        height: 384px;
        width: 100%;
        object-fit: cover;
        @include sm-block {
            height: 320px;
        }
    }
}

.blog-head__info {
    padding: 48px;
    position: relative;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 202px;
    &::before {
        position: absolute;
        z-index: 1;
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        left: 0;
        bottom: 0;
        right: 0;
        top: 0;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
    }
    .container {
        position: relative;
        z-index: 2;
    }
    @include sm-block {
        padding: 37px 0;
        height: auto;
    }
}

.blog-head__date {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    margin-bottom: 16px;
}
.blog-head__title {
    font-size: 28px;
    line-height: 32px;
    font-weight: 700;
    @include sm-block {
        font-size: 20px;
        line-height: 28px;
    }
}

.blog-info {
    max-width: 1010px;
    margin: 0 auto;
    padding: 0 40px;
    padding-top: 48px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include md-block {
        padding: 40px 20px 0;
        flex-direction: column-reverse;

    }
}

.blog__asside {
    max-width: 258px;
    width: 100%;
    @include md-block {
        max-width: 100%;
        display: flex;
        flex-direction: column-reverse;
    }
}

.blog__links {
    @include md-block {
        margin-bottom: 40px;
    }
}

.blog__nav {
    padding: 22px 24px;
    background-color: #232A30;
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 48px;
    @include md-block {
        width: calc(100% + 40px);
        margin: 0 -20px 40px;
    }
}

.blog__nav-title {
    margin-bottom: 16px;
    color: #DBDBDB;
}

.blog-nav__list {
    counter-reset: section;
}

.blog__nav-item {
    font-size: 14px;
    line-height: 22px;
    padding-left: 28px;
    color: #DBDBDB;
    margin-bottom: 16px;
    position: relative;
    counter-increment: section;
    &::before {
        
        content: counter(section)'.';
        position: absolute;
        left: 0;
        top: 0;
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        color: $color-white;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.blog__main {
    max-width: 576px;
    font-size: 20px;
    line-height: 32px;
    color: $color-text;
    @include md-block {
        max-width: 100%;
    }
    @include sm-block {
        font-size: 16px;
        line-height: 32px;
    }
}

.blog__section {
    margin: 48px 0;
    &:first-of-type {
        margin-top: 0;
    }

    h3 {
        margin-bottom: 24px;
        color: $color-white;
    }
    ul {
        margin: 24px 0;
        li {
            padding-left: 24px;
            position: relative;
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 12px;
                left: 8px;
                width: 3px;
                height: 3px;
                background-color: $color-text;
                border-radius: 50%;
            }
            + li {
                margin-top: 24px;
            }
        }
    }
    p {
        + p {
            margin-top: 24px;
        }
    }
    @include md-block {
        margin: 40px 0;
    }
    @include sm-block {
        h3 {
            font-size: 18px;
            line-height: 22px;
        }
    }
}

.blog__section-img {
    @include md-block {
        width: calc(100% + 40px);
        margin: 0 -20px;
        img {
            width: 100%;
            display: block;
        }
    }
}