.achievements {
    margin-top: -120px;
    @include sm-block {
        margin-top: -20px;
    }
}

.achievements-content {
    background: 
    url('../images/achievements-bg.png') center center / cover no-repeat,
    #10152B;
    border: 1px solid $color-secondary;
    box-shadow: 0px 0px 12px rgba(100, 21, 229, 0.5);
    border-radius: 16px;
    padding: 24px;
}

.achievements__title {
    text-transform: uppercase;
    text-align: center;
    @include font-main();
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 40px;
    color: $color-secondary;
}

.achievements__list  {
    display: flex;
    justify-content: space-between;
    max-width: 962px;
    margin: 0 auto;
    @media(max-width: 1200px) {
        flex-wrap: wrap;
        padding: 0 56px;
        gap: 27.5px;
    }
    @include sm-block {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
        gap: 48px 18px;
        padding: 0;
    }
}

.achievements__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-size: 16px;
    line-height: 2;
    span {
        display: block;
        margin-bottom: 3px;
        font-family: 'Orbitron';
        font-size: 28px;
        line-height: 1.4;
        text-transform: uppercase;
        font-weight: 700;
    }
    @include sm-block {
        line-height: 1.5;
        &:nth-of-type(3) {
            grid-row: 3 / 4;
            grid-column: span 2;
        }
        span {
            font-size: 20px;
            line-height: 1.6;
        }
    }
}