@font-face {
    font-family: 'Orbitron';
    src: url('../../fonts/Orbitron-Bold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'Orbitron';
    src: url('../../fonts/Orbitron-SemiBold.ttf') format('truetype');
    font-weight: 800;
    font-style: normal;
}

@font-face {
    font-family: 'Raleway';
    src: 
    url('../../fonts/Raleway-Regular.ttf') format('truetype'),
    url('../../fonts/Raleway-Regular.eot') format('eot'),
    url('../../fonts/Raleway-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'Raleway';
    src: 
    url('../../fonts/Raleway-Medium.ttf') format('truetype'),
    url('../../fonts/Raleway-Medium.eot') format('eot'),
    url('../../fonts/Raleway-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Raleway';
    src: 
    url('../../fonts/Raleway-Semibold.ttf') format('truetype'),
    url('../../fonts/Raleway-Semibold.eot') format('eot'),
    url('../../fonts/Raleway-Semibold.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}
