@mixin section-divider {
    position: relative;
    &::before {
        display: block;
        position: absolute;
        content: '';
        bottom: 10px;
        width: 100%;
        height: 2px;
        background: rgb(255,255,255);
        background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.20772058823529416) 50%, rgba(255,255,255,0) 100%);
        z-index: 2;
    }
}


@mixin xl-block {
    @media(max-width: 1440px) {
        @content
    }
}

@mixin lg-block {
    @media(max-width: 1200px) {
        @content
    }
}

@mixin md-block {
    @media(max-width: 1024px) {
        @content
    }
}

@mixin sm-block {
    @media(max-width: 767px) {
        @content
    }
}

@mixin xs-block {
    @media(max-width: 495px) {
        @content
    }
}

@mixin font-main {
    font-family: 'Orbitron';
}