.footer {
    padding: 100px 0 60px;
    background: #050B10;
    .container {
        max-width: 1370px;
        width: 100%; 
    }

    @include lg-block {
        padding: 64px 0;
    }

    @include md-block {
        .container {
            padding: 0 20px;
        }
    }
    
    @include sm-block {
        .container {
            padding: 0 15px;
        }
    }
}

.footer-main {
    display: flex;
    margin-bottom: 72px;
    @include lg-block {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 44px 1fr;
        grid-gap: 32px 24px;
        margin-bottom: 32px;
    }
    @include sm-block {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        gap: 0;
    }
}

.footer__logo {
    @include lg-block {
        grid-row: 1 / 2;
        grid-column: 1 / 2;
    }
    @include sm-block {
        grid-row: inherit;
        grid-column: inherit;
        display: block;
        margin-bottom: 32px;
    }
}

.footer-nav {
    max-width: 400px;
    width: 100%;
    margin: 0 40px 0 auto;
    @include lg-block {
        margin: 0;
        grid-row: 2 / 3;
        grid-column: 1 / 2;
    }
    @include sm-block {
        grid-row: inherit;
        grid-column: inherit;
        margin-bottom: 24px;
    }
}

.footer-nav__list {
    column-count: 2;
    width: 100%;
    @include lg-block {
        column-count: 1;
    }
}

.footer-nav__item {
    display: block;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 16px;
    @include lg-block {
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}


.footer__contacts {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    max-width: 516px;
    width: 100%;
    @include lg-block {
        flex-direction: column;
        grid-row: 2 / 3;
        grid-column: 2 / 3;
    }
    @include sm-block {
        grid-row: inherit;
        grid-column: inherit;
    }
}

.footer__contacts-col {
    @include lg-block {
        margin-bottom: 16px;
    }
}

.footer__mail {
    display: block;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 16px;
}

.footer__adress {
    max-width: 186px;
    font-weight: 500;
    color: $color-text;
    @include lg-block {
        max-width: 237px;
    }
}


.socials  {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 40px;
    @include lg-block {
        justify-content: flex-start;
        flex-wrap: wrap;
    }
    @include xs-block {
        flex-direction: column;
        width: 100%;
    }
}

.socials-col {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 40px;
    @include sm-block {
        width: 100%;
    }
}

.socials__item {
    display: block;
    svg {
        display: block;
        fill: #BFBFBF;
    }
    &:hover {
        svg {
            fill:$color-primary;
        }
    }
    @include xs-block {
        svg {
            display: block;
            margin: 0 auto;
        }
    }
}