.slider__btn {
    position: absolute;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: linear-gradient(140.19deg, rgba(217, 217, 217, 0.31) -7.53%, rgba(217, 217, 217, 0) 87.78%);
    cursor: pointer;
    &:hover {
        opacity: .5;
    }
}

.slider__btn--next {
    position: absolute;
    svg {
        position: absolute;
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-90deg);
    }
}

.slider__btn--prev {
    position: absolute;
    svg {
        position: absolute;
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(90deg);
    }
}

.slider__btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    @include sm-block {
        display: none;
    }
}
.slider__btn--next {
    right: 0;
}
.slider__btn--prev {
    left: 0;
}


.swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
    &-bullet {
        width: 12px;
        height: 12px;
        display: block;
        margin: 0 8px;
        opacity: .2;
        background: #D9D9D9;
        &-active {
            opacity: .6;
        }
    }
}
